import { useContext, useState } from "react"
import axiosInstance, { REQUEST_TYPES } from "./apiUtil"
import { UserContext } from "./UserContextProvider"

const useApi = (url, type=REQUEST_TYPES.GET,{upatedUserData=true}={}) => {

    const {isError,isLoading,message,setIsError,setIsLoading,setMessage,setUserData}=useContext(UserContext)
    // const [loading,setLoading]=useState(null)
    // const [error,setError]=useState(null)
    // const [data,setData]=useState(null)
    // const [message,setMessage]=useState(null)
    const [response,setResponse]=useState(null)

  const makeRequest=async(payload)=>{
      try {
        setIsError(false)
        setMessage(null)
        setIsLoading(true)
        const {data:response}=await axiosInstance[type](url,payload);
        const {data,message}=response
        // (userData)=>({...userData,...data}))
        if(upatedUserData){
          setUserData(data)
        }else{
          setResponse(response)
        }
        
        setUserData(data)
        setMessage(message)
    } catch (error) {
        setIsError(true)
        const errData=error.response?.data
        const message=errData?.message
        console.log({errData})
        setMessage(message)
    }finally{
        setIsLoading(false)
    }
  }
  return {makeRequest,isLoading,isError,message,response,setResponse}
}

export default useApi