import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import ProductCard from "../ProductCard";
import { Container, Row, Col } from "react-bootstrap";
import { useSearchParams } from "react-router-dom";
import { UserContext } from "../UserContextProvider";

function Products() {
  const URL = "https://fakestoreapi.com/products";
  const [products, setProducts] = useState([]);
  const [searchParams] = useSearchParams();
  const [filters,setFilters]=useState([]);
  const [activeFilters, setActiveFilters]=useState([]);

//   const {userData={cart:[]}={}}=useContext(UserContext)
  const {userData,isLoading,setIsLoading}=useContext(UserContext)
  
  const cart=userData?.cart
  
  console.log([...searchParams.entries()]);
  const search = ({ title, description, category }) => {
    const searchTerm = searchParams.get("search");
    
    if (searchTerm) {
      return (
        title.toLowerCase().includes(searchTerm) ||
        description.toLowerCase().includes(searchTerm) ||
        category.toLowerCase().includes(searchTerm)
      );
    }

    return true;
  };

  useEffect(() => {
    (async () => {
      setIsLoading(true)
      const { data } = await axios.get(URL);
      setIsLoading(false)
      setProducts(data);
    })();
  }, []);

  return (
    <Container fluid>
      <Row>
        {/* <Col sm={{span:10, offset:1}} md={{span:5, offset:0 }} lg={{span:4}} xl={{span:3}}> */}
        {products.filter(search).map((product) => (
          <ProductCard isLoading={isLoading} cart={cart} key={product.id} product={product} isLoggedIn={!!userData}/>
        ))}
        {/* </Col> */}
      </Row>
    </Container>
  );
}

export default Products;
