import React, { useState } from "react";
import { Button, Container, Col, Row, CardBody, Card } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  decrementActionCreator,
  incrementActionCreator,
} from "../reducers/CountReducers";

const Counter = () => {
  // const [count,setCount]=useState(0);
  const [value, setValue] = useState(1);
  const { count } = useSelector((state) => state);

  const dispatch = useDispatch();

  // const valueFromApi=Promise.resolve(10)

  const increment = (count) => {
    dispatch(incrementActionCreator(value));
    // setCount(count=>count+1)
  };
  const decrement = () => {
    dispatch(decrementActionCreator(value));
    // setCount(count=>count-1)
  };
  return (
    <Container>
      <Row>
        <Col span={4} offset={4}>
          <Card className="mt-5">
            <CardBody>
              <div>
                <input
                  type="number"
                  min={1}
                  max={10}
                  onChange={(e) => setValue(+e.target.value)}
                />
              </div>
              <Button variant="outline-success" onClick={increment}>
                +
              </Button>
              <span>{count}</span>
              <Button variant="outline-success" onClick={decrement}>
                -
              </Button>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Counter;
