import React, { useContext, useEffect, useState } from "react";
import {
  Container,
  Row,
  Col,
  FormGroup,
  FormLabel,
  FormControl,
  Card,
  Button,
  Form,
} from "react-bootstrap";
import "./style.scss";
import axiosInstance, { ENDPOINTS, REQUEST_TYPES } from "../apiUtil";
import useApi from "../useApi";
import { UserContext } from "../UserContextProvider";
import { useLocation, useNavigate } from "react-router";

const Login = () => {
  const { isLoading, makeRequest, message, error } = useApi(
    ENDPOINTS.USER.LOGIN,
    REQUEST_TYPES.POST
  );
  const {
    makeRequest: resetPassword,
    response,
    setResponse,
  } = useApi(ENDPOINTS.USER.RESET_PASSWORD, REQUEST_TYPES.PATCH, {
    upatedUserData: false,
  });

  const { userData } = useContext(UserContext);

  const { state } = useLocation();
  const navigate = useNavigate();

  const [username, setUsername] = useState(null);
  const [password, setpassword] = useState(null);

  const [showResetForm, setShowResetForm] = useState(false);
  const [otp, setOtp] = useState(null);

  const isValid = username && password;
  useEffect(()=>{if(userData?.username){navigate('/')}},[userData])
  useEffect(() => {
    if (userData && state?.redirectionURL) {
      navigate(state.redirectionURL, { replace: true });
    }
  }, [userData, state,navigate]);

  useEffect(() => {
    if (
      showResetForm &&
      response?.message === "Password reset successfully" &&
      !error
    ) {
      setShowResetForm(false);
      setResponse(null);
    }
  }, [showResetForm, response]);
  const login = () => {
    const payload = { username, password };
    makeRequest(payload);
    console.log("login-----------", { userData, message, error });
    // try {

    //     const payload={username, password}
    //     const {data}=await axiosInstance.post(ENDPOINTS.USER.LOGIN,payload)
    //     console.log(data)
    //     console.log(data.data)
    //     console.log(data.message)
    // } catch (error) {
    //     const errData=error.response.data
    //     // console.log(.message)
    //     alert(errData.message)
    // }
  };

  return (
    <Container fluid>
      <Row>
        <Col
          lg={{ span: 4, offset: 4 }}
          md={{ span: 6, offset: 3 }}
          sm={{ span: 10, offset: 1 }}
        >
          <Card className="mt-5 login">
            <Card.Body>
              <FormGroup className="mb-3">
                <FormLabel>Username</FormLabel>
                <FormControl
                  placeholder="Username"
                  onChange={(e) => setUsername(e.target.value)}
                ></FormControl>
              </FormGroup>
              <FormGroup className="mb-3">
                <FormLabel>Password</FormLabel>
                <FormControl
                  placeholder="Password"
                  type="password"
                  onChange={(e) => setpassword(e.target.value)}
                ></FormControl>
              </FormGroup>
              <FormGroup className="mb-3">
                {/* <Button onClick={login} disabled={!isValid} variant='outline-primary' className='mt-3'>Login</Button> */}
                {showResetForm ? (
                  <>
                    <Form.Group className="mb-3" controlId="otp">
                      <Form.Label>OTP</Form.Label>
                      <Form.Control
                        type="number"
                        placeholder="Code from Authenticator"
                        onChange={(e) => setOtp(e.target.value)}
                      />
                    </Form.Group>
                    <Button
                      variant="outline-primary"
                      onClick={() => {
                        resetPassword({ username, password, otp });
                      }}
                      disabled={!isValid && !otp}
                    >
                      Reset Password
                    </Button>
                  </>
                ) : (
                  <div className="d-flex justify-content-between">
                    <Button
                      variant="outline-primary"
                      onClick={login}
                      disabled={!isValid}
                    >
                      Login
                    </Button>

                    <Button
                      variant="outline-danger"
                      onClick={() => {
                        setShowResetForm(true);
                      }}
                    >
                      Forgot Password
                    </Button>
                  </div>
                )}
              </FormGroup>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Login;
