import { Badge, Button, Container, Form, Nav,  Navbar } from "react-bootstrap";
import { Link, useSearchParams } from "react-router-dom";

import 'bootstrap/dist/css/bootstrap.min.css';
import { useContext } from "react";
import { UserContext } from "./UserContextProvider";
import { Cart } from "react-bootstrap-icons";
import useApi from "./useApi";
import { ENDPOINTS } from "./apiUtil";


const MyNavbar = () => {
  const [,setSearchParams]=useSearchParams()
  const {userData}=useContext(UserContext)
  const {makeRequest}=useApi(ENDPOINTS.USER.LOGOUT)
  
  // const {cart:{totalCount}={}}=userData || {}
  const totalCount=userData?.totalCount
  return (
<Navbar expand="lg" className="bg-body-tertiary" >
      <Container fluid>
        <Navbar.Brand as={Link} to="/">amazon</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Nav.Link as={Link} to="">Home</Nav.Link>
            {/* <Nav.Link as={Link} to="/reducers">Reducers</Nav.Link> */}
            
          </Nav>
          <Nav className="d-flex align-items-center">
          {
            userData?<>
            <Nav.Link as={Link} to="/cart"><Cart  size={25}/></Nav.Link>
            {totalCount && <Badge style={{position:"relative", top:"-10px",left:"-10px",display:"flex",alignItems:"center"}} pill bg="primary">{totalCount}</Badge>}
            <Nav.Link onClick={makeRequest}>Logout</Nav.Link>
            </>:<>
            
          <Nav.Link as={Link} to="/login">Login</Nav.Link>
          <Nav.Link as={Link} to="/signup">Sign Up</Nav.Link>
            </>
          }
          </Nav>
          <Form className="d-flex">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
              onChange={e=>setSearchParams({search:e.target.value.toLowerCase()})}
            />
            {/* <Button variant="outline-primary" onClick={e=>setSearchParams({search:e.target.value.toLowerCase() })}>Search</Button> */}
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}


export default MyNavbar