import React from 'react'
import { Button } from 'react-bootstrap'
import useApi from '../useApi'
import { ENDPOINTS, REQUEST_TYPES } from '../apiUtil'

const CartComponent = () => {
    const {makeRequest}=useApi(ENDPOINTS.CART.CLEAR_CART,REQUEST_TYPES.PUT)
  return (
    <Button className='m-4' variant='outline-danger' onClick={()=>makeRequest()}>Clear Cart</Button>
    
  )
}

export default CartComponent