import React from 'react'
import { Badge, Button } from 'react-bootstrap'
import { CartPlus, TrashFill } from 'react-bootstrap-icons'
import "./styles.scss"
import useApi from '../useApi'
import { ENDPOINTS, REQUEST_TYPES } from '../apiUtil'

const CartCounter = ({ product,disabled}) => {
  const {quantity}=product
  // console.log("---quantity",quantity)
  const {makeRequest:increment}=useApi(ENDPOINTS.CART.INCREMENT, REQUEST_TYPES.PATCH)
  const {makeRequest:decrement}=useApi(ENDPOINTS.CART.DECREMENT, REQUEST_TYPES.PATCH)
  return (
    <>
        <Button variant='outline-success' disabled={disabled} className='round-btn' onClick={()=>increment(product)}>+</Button>
            <Badge pill bg='info mx-1'>{quantity}</Badge>
        <Button variant='outline-success'  disabled={disabled} className='round-btn' onClick={()=>decrement(product)}>-</Button>
    </>
  )
}

export default CartCounter