import { BrowserRouter, Route, Routes } from "react-router-dom";
import "./Products/index.jsx";
import Products from "./Products/index.jsx";
import MyNavbar from "./MyNavbar.jsx";
import RoutingDemo from "./RoutingDemo/index.jsx";
import Parent from "./RoutingDemo/Parent.jsx";
import Child from "./RoutingDemo/Child.jsx";
import Login from "./Login/index.jsx";
import SignUp from "./Signup/index.jsx";
import Counter from "./Counter/index.js";
import useApi from "./useApi.js";
import { ENDPOINTS } from "./apiUtil.js";
import { useContext, useEffect } from "react";
import Loading from "./Loader/index.jsx";
import { UserContext } from "./UserContextProvider.jsx";
import Toast from "./Toast.jsx";
import Bootstrap from "./Bootstrap.jsx";   
import CartComponent from "./CartComponent/index.jsx";

function App() {
  const {makeRequest: login,message} = useApi(ENDPOINTS.USER.LOGIN);
  useEffect(() => {
    login();
  }, []);
  const {isLoading}=useContext(UserContext)
  return (
    <>
      <BrowserRouter>
        <Toast/>
        <Loading isLoading={isLoading} />
        <MyNavbar />
        <Routes>
          <Route path="/routing/:productId" element={<RoutingDemo />} />

          <Route path="/" element={<Products />} />
          <Route path="/cart" element={<CartComponent />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<SignUp />} />
          <Route path="/reducers" element={<Counter />} />
          <Route path="/parent" element={<Parent />}>
            <Route path="child" element={<Child />}></Route>
            <Route path="child1" element={<h3>Md irfan ansari</h3>}></Route>
            <Route path="child2" element={<h4>Md irfan ansari</h4>}></Route>
          </Route>
        </Routes>
      </BrowserRouter>
      {/* <Products/> */}
      {/* <Bootstrap/> */}
    </>
  );
}

export default App;
